<template>
  <v-container>
    <v-row style="max-height: 480px">
      <div ref="video" style="max-height: 480px; max-width: 640px;">
      </div>
    </v-row>
    <v-row style="max-width: 2in">
      <v-container>
        <v-row v-for="item in currentItems" :key="item.barcodeId">
          <v-col cols="10" class="text-right">
            {{item.name}}
          </v-col>
          <v-col cols="2" class="text-left">
            ${{item.price}}
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="10" class="text-right">
            Total:
          </v-col>
          <v-col cols="2" class="text-left">
            {{total}}
          </v-col>
        </v-row>
      </v-container>
    </v-row>
    <v-row>
      <v-btn @click="finish">
        Finish
      </v-btn>
    </v-row>
  </v-container>
</template>
<script>
import Quagga from 'quagga';
import Table from '../util/Table';

export default {
  data: () => ({
    currentItems: [],
    items: [],
    table: new Table(),
    currentCallback: null,
    detectionInLastSecond: false,
  }),
  computed: {
    total() {
      let total = 0;
      this.currentItems.forEach((item) => {
        total += +item.price;
      });
      return total;
    },
  },
  methods: {
    async start() {
      if (!this.currentCallback) {
        this.currentCallback = Quagga.onDetected(data => this.onDetection(data));
      }
      this.items = await this.table.get();

      Quagga.init({
        inputStream: {
          name: 'Live',
          type: 'LiveStream',
          target: this.$refs.video,
        },
        decoder: {
          readers: ['code_128_reader'],
        },
      }, (err) => {
        if (err) {
          console.log(err);
          return;
        }
        console.log('Initialization finished. Ready to start');
        Quagga.start();
      });
    },
    onDetection(data) {
      if (this.detectionInLastSecond) {
        return;
      }

      console.log(data.codeResult);
      // eslint-disable-next-line
      const beep = new Audio('data:audio/wav;base64,');
      beep.play();
      this.currentItems
        .push(this.items
          .filter(item => item.barcodeId === +data.codeResult.code)[0]);
      this.currentItems = [...this.currentItems];
      this.detectionInLastSecond = true;
      setTimeout(() => { this.detectionInLastSecond = false; }, 1000);
    },

    finish() {
      Quagga.offDetected(this.currentCallback);
      Quagga.stop();
      this.currentCallback = null;
      this.$emit('finish', this.currentItems);
    },
  },
};
</script>
